@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box;font-family:"Open Sans",sans-serif}.body{height:100vh}.container{max-width:1300px;width:100%;padding-left:15px;padding-right:15px;margin:0 auto}.page{height:calc(100vh - 60px - 40px)}p,h1,h2,h3,h4,h5,h6{margin:0;padding:0}button{cursor:pointer}button:disabled{color:rgba(255,255,255,.6);cursor:not-allowed}.btn{border:none;background-color:transparent;color:#fff;font-size:15px;transition:all .6s}.btn:hover{color:rgba(255,255,255,.6)}.btn--main{padding:10px 20px;background-color:#fff;color:#000;border-radius:100px}.btn--main:hover{background-color:#e7e7e7;color:#000}.btn--main:disabled{background-color:rgba(231,231,231,.7);color:#000}input[type=range]{cursor:pointer}input[type=range]:disabled{cursor:not-allowed}
.bar{display:flex;align-items:flex-end;justify-content:center;height:100%;width:100%;overflow:hidden}.bar-container{background-color:#2ca08b;display:flex;justify-content:center;position:relative;border-radius:3px}.bar-container--small>*{display:none}.bar-container p{color:#fff;font-size:18px;margin-top:10px}.bar-container:not(.bar-container:last-child){margin-right:2px}
.home{background-color:#ebebeb;display:flex;align-items:center}
.algorithms-list{display:flex}.algorithms-list button:not(.algorithms-list button:last-child){margin-right:30px}
.secondary-buttons{display:flex}.secondary-buttons button:not(.secondary-buttons button:last-child){margin-right:25px}
.array-size{display:flex}
.animation-speed{display:flex}
.navbar{overflow:hidden;background-color:#000;color:#fff}.navbar__top{display:flex;align-items:center;justify-content:space-between;height:60px}.navbar__bottom{display:flex;align-items:center;height:40px}
